import type { Meta, StoryFn } from '@storybook/web-components';

import '@tiger-analytics/ui';

import { html } from 'lit';

export default {
  title: 'Grid/ta-grid-column',
  component: 'ta-grid-column',
  parameters: {
    controls: { expanded: true },
  },
  argTypes: {
    sm: {
      control: 'number',
      type: 'number',
      description: 'number of columns to use on small screen',
      table: {
        defaultValue: { summary: '6' },
      },
    },
    md: {
      control: 'number',
      type: 'number',
      description: 'number of columns to use on medium screen',
      table: {
        defaultValue: { summary: '12' },
      },
    },
    lg: {
      control: 'number',
      type: 'number',
      description: 'number of columns to use on large screen',
      table: {
        defaultValue: { summary: '12' },
      },
    },
  },
} as Meta;

const description = `The <ta-grid-column> web component is used to create child elements within a grid container, allowing for flexible column spans. You can specify the number of columns the element should occupy using the span attribute. This enables easy control over the width of child elements relative to the grid container. <ta-grid-column> is designed to be used inside a <ta-grid-container>, and its span value determines how much space it should occupy in the grid, ensuring a fluid and adaptive layout.`;

const Template: StoryFn = (args) => html`
  <ta-grid-container style="background: violet;" .showSideMargin="${false}">
    <ta-grid-column
      .sm="${args.sm}"
      .md="${args.md}"
      .lg="${args.lg}"
      style="background: yellowgreen;"
      >Column</ta-grid-column
    >
  </ta-grid-container>
  <ta-grid-container .showSideMargin="${false}">
    <ta-grid-column
      ><div class="story-title">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</div></ta-grid-column
    >
    <ta-grid-column><div class="story-description">${description}</div></ta-grid-column>
  </ta-grid-container>
`;

export const CodeAndAccessibility = Template.bind({});
CodeAndAccessibility.args = {
  sm: 6,
  md: 12,
  lg: 12,
};
